			PromotionCode: '', // 二维码图片
			canvasStatus: false, // 海报绘图标签
			posterImage: '', // 海报路径
			posterbackgd: '/static/images/posterbackgd.png',
			sharePacket: {
				isState: true // 默认不显示
			}, // 分销商详细
			uid: 0, // 用户uid
			circular: false,
			autoplay: false,
			interval: 3000,
			duration: 500,
			clientHeight: '',
			systemStore: {}, // 门店信息
			good_list: [],
			replyChance: 0,
			CartCount: 0,
			isDown: true,
			storeSelfMention: true,
			posters: false,
			weixinStatus: false,
			attr: {
				cartAttr: false,
				productAttr: [],
				productSelect: {}
			},
			description: '',
			navActive: 0,
			H5ShareBox: false, // 公众号分享图片
			activity: [],
			navH: '',
			navList: [],
			opacity: 0,
			scrollY: 0,
			topArr: [],
			toView: '',
			height: 0,
			heightArr: [],
			lock: false,
			scrollTop: 0,
			tagStyle: {
				img: 'width:100%;display:block;',
				table: 'width:100%',
				video: 'width:100%'
			},
			returnShow: true, // 判断顶部返回是否出现
			diff: '',
			is_money_level: 1,
			is_vip: 0, // 是否是会员
			navbarRight: 0,
			homeTop: 20,
			routineContact: 0,
			skuArr: [],
			selectSku: {}
		}
	},
	computed: mapGetters([ 'isLogin' ]),
	watch: {
		isLogin: {
			handler(newV, oldV) {
				if (newV == true) {
					this.getCouponList()
					this.getCartCount()
					this.downloadFilePromotionCode()
				}
			},
			deep: true
		},
		storeInfo: {
			handler() {
				this.$nextTick(() => {})
			},
			immediate: true
		}
	},
	onLoad(options) {
		const that = this
		var pages = getCurrentPages()
		that.returnShow = pages.length !== 1
		// #ifdef MP
		that.navH = app.globalData.navHeight
		// #endif
		// #ifndef MP
		that.navH = 96
		// #endif
		that.id = options.id
		uni.getSystemInfo({
			success(res) {
				that.height = res.windowHeight
				// res.windowHeight:获取整个窗口高度为px，*2为rpx；98为头部占据的高度；
				// #ifndef APP-PLUS || H5 || MP-ALIPAY
				that.navbarRight =
          res.windowWidth - uni.getMenuButtonBoundingClientRect().left
				// #endif
			}
		})
		// 扫码携带参数处理
		// #ifdef MP
		if (options.scene) {
			const value = that.$util.getUrlParams(decodeURIComponent(options.scene))
			if (value.id) options.id = value.id
			// 记录推广人uid
			if (value.pid) app.globalData.spid = value.pid
		}
		if (!options.id) {
			this.showSkeleton = false
			return that.$util.Tips(
				{
					title: '缺少参数无法查看商品'
				},
				{
					tab: 3,
					url: 1
				}
			)
		}
		that.id = options.id

		// 记录推广人uid
		if (options.spid) app.globalData.spid = options.spid
		// #endif
		that.getGoodsDetails()
		// that.getCouponList(0);
		// #ifdef H5
		that.isLogin && silenceBindingSpread()
		// #endif
	},
	onReady() {
		this.isNodes++
		this.$nextTick(function () {
			// #ifdef MP
			const menuButton = uni.getMenuButtonBoundingClientRect()
			const query = uni.createSelectorQuery().in(this)
			query
				.select('#home')
				.boundingClientRect((data) => {
					this.homeTop = menuButton.top * 2 + menuButton.height - data.height
				})
				.exec()
			// #endif
			// #ifdef H5
			const clipboard = new ClipboardJS('.copy-data')
			clipboard.on('success', () => {
				this.$util.Tips({
					title: '复制成功'
				})
			})
			// #endif
		})
	},

	/**
   * 用户点击右上角分享
   */
	// #ifdef MP
	onShareAppMessage() {
		const that = this
		that.$set(that, 'actionSheetHidden', !that.actionSheetHidden)
		userShare()
		return {
			title: that.storeInfo.store_name || '',
			imageUrl: that.storeInfo.image || '',
			path: '/pages/goods_details/index?id=' + that.id + '&spid=' + that.uid
		}
	},
	// #endif
	methods: {
		jumpUrl(url) {
			uni.switchTab({
				url
			})
		},
		videoPause() {
			this.$nextTick(() => {
				that.infoScroll()
			})
		},
		// app分享
		// #ifdef APP-PLUS
		appShare(scene) {
			const that = this
			const routes = getCurrentPages() // 获取当前打开过的页面路由数组
			const curRoute = routes[routes.length - 1].$page.fullPath // 获取当前页面路由，也就是最后一个打开的页面路由
			uni.share({
				provider: 'weixin',
				scene,
				type: 0,
				href: `${HTTP_REQUEST_URL}${curRoute}&spread=${that.uid}`,
				title: that.storeInfo.store_name,
				summary: that.storeInfo.store_info,
				imageUrl: that.storeInfo.small_image,
				success(res) {
					uni.showToast({
						title: '分享成功',
						icon: 'success'
					})
					that.posters = false
				},
				fail(err) {
					uni.showToast({
						title: '分享失败',
						icon: 'none',
						duration: 2000
					})
					that.posters = false
				}
			})
		},
		// #endif
		closeChange() {
			this.$set(this.sharePacket, 'isState', true)
		},
		boxStatus(data) {
			this.showAnimate = data
		},
		goActivity(e) {
			const item = e
			if (item.type === '1') {
				uni.navigateTo({
					url: `/pages/activity/goods_seckill_details/index?id=${item.id}&time=${item.time}&status=1`
				})
			} else if (item.type === '2') {
				uni.navigateTo({
					url: `/pages/activity/goods_bargain_details/index?id=${item.id}&bargain=${this.uid}`
				})
			} else {
				uni.navigateTo({
					url: `/pages/activity/goods_combination_details/index?id=${item.id}`
				})
			}
		},

		/**
     * 购物车手动填写
     *
     */

		iptCartNum(e) {
			this.$set(this.attr.productSelect, 'cart_num', e)
		},
		// 后退
		returns() {
			uni.navigateBack()
		},
		tap(index) {
			var id = 'past' + index
			var index = index
			var that = this
			// if (!this.data.good_list.length && id == "past2") {
			//   id = "past3"
			// }
			this.$set(this, 'toView', id)
			this.$set(this, 'navActive', index)
			this.$set(this, 'lock', true)
			this.$set(
				this,
				'scrollTop',
				index > 0
					? that.topArr[index] - app.globalData.navHeight / 2
					: that.topArr[index]
			)
		},
		scroll(e) {
			var that = this
			var scrollY = e.detail.scrollTop
			var opacity = scrollY / 200
			opacity = opacity > 1 ? 1 : opacity
			that.$set(that, 'opacity', opacity)
			that.$set(that, 'scrollY', scrollY)
			that.$set(that, 'showAnimate', false)
			that.$set(that, 'showMenuIcon', false)
			if (that.lock) {
				that.$set(that, 'lock', false)
				return
			}
			for (var i = 0; i < that.topArr.length; i++) {
				if (
					scrollY <
          that.topArr[i] - app.globalData.navHeight / 2 + that.heightArr[i]
				) {
					that.$set(that, 'navActive', i)
					break
				}
			}
		},

		/*
     *去商品详情页
     */

		goDetail(item) {
			if (item.activity.length == 0) {
				uni.redirectTo({
					url: '/pages/goods_details/index?id=' + item.id
				})
				return
			}
			// 砍价
			if (item.activity && item.activity.type == 2) {
				uni.redirectTo({
					url: `/pages/activity/goods_bargain_details/index?id=${item.activity.id}&bargain=${this.uid}`
				})
				return
			}
			// 拼团
			if (item.activity && item.activity.type == 3) {
				uni.redirectTo({
					url: `/pages/activity/goods_combination_details/index?id=${item.activity.id}`
				})
				return
			}
			// 秒杀
			if (item.activity && item.activity.type == 1) {
				uni.redirectTo({
					url: `/pages/activity/goods_seckill_details/index?id=${item.activity.id}&time=${item.activity.time}&status=1`
				})
			}
		},
		// 微信登录回调
		onLoadFun(e) {
			// this.getUserInfo();
			// this.get_product_collect();
		},
		ChangCouponsClone() {
			this.$set(this.coupon, 'coupon', false)
		},

		/*
     * 获取用户信息
     */

		getUserInfo() {
			const that = this
			getUserInfo().then((res) => {
				that.$set(that, 'uid', res.data.uid)
				that.$set(that, 'is_money_level', res.data.is_money_level)
			})
		},

		/**
     * 购物车数量加和数量减
     *
     */

		ChangeCartNum(changeValue) {
			// changeValue:是否 加|减
			// 获取当前变动属性
			let productSelect = this.productValue[this.attrValue]
			// 如果没有属性,赋值给商品默认库存
			if (productSelect === undefined && !this.attr.productAttr.length) productSelect = this.attr.productSelect
			// 无属性值即库存为0；不存在加减；
			if (productSelect === undefined) return
			const stock = productSelect.stock || 0
			const num = this.attr.productSelect
			if (changeValue) {
				num.cart_num++
				if (num.cart_num > stock) {
					this.$set(this.attr.productSelect, 'cart_num', stock || 1)
					this.$set(this, 'cart_num', stock || 1)
				}
			} else {
				num.cart_num--
				if (num.cart_num < 1) {
					this.$set(this.attr.productSelect, 'cart_num', 1)
					this.$set(this, 'cart_num', 1)
				}
			}
		},
		attrVal(val) {
			this.$set(
				this.attr.productAttr[val.indexw],
				'index',
				this.attr.productAttr[val.indexw].attr_values[val.indexn]
			)
		},

		/**
     * 属性变动赋值
     *
     */

		ChangeAttr(res) {
			const productSelect = this.productValue[res]
			this.$set(this, 'selectSku', productSelect)
			if (productSelect && productSelect.stock > 0) {
				this.$set(this.attr.productSelect, 'image', productSelect.image)
				this.$set(this.attr.productSelect, 'price', productSelect.price)
				this.$set(this.attr.productSelect, 'stock', productSelect.stock)
				this.$set(this.attr.productSelect, 'unique', productSelect.unique)
				this.$set(this.attr.productSelect, 'cart_num', 1)
				this.$set(
					this.attr.productSelect,
					'vip_price',
					productSelect.vip_price
				)
				this.$set(this, 'attrValue', res)
				this.$set(this, 'attrTxt', '已选择')
			} else {
				this.$set(this.attr.productSelect, 'image', this.storeInfo.image)
				this.$set(this.attr.productSelect, 'price', this.storeInfo.price)
				this.$set(this.attr.productSelect, 'stock', 0)
				this.$set(this.attr.productSelect, 'unique', '')
				this.$set(this.attr.productSelect, 'cart_num', 0)
				this.$set(
					this.attr.productSelect,
					'vip_price',
					this.storeInfo.vip_price
				)
				this.$set(this, 'attrValue', '')
				this.$set(this, 'attrTxt', '请选择')
			}
		},

		/**
     * 领取完毕移除当前页面领取过的优惠券展示
     */

		ChangCoupons(e) {
			const coupon = e
			const couponList = this.$util.ArrayRemove(this.couponList, 'id', coupon.id)
			this.$set(this, 'couponList', couponList)
			this.getCouponList()
		},

		setClientHeight() {
			const that = this
			if (!that.good_list.length) return
			const view = uni.createSelectorQuery().in(this)
				.select('#list0')
			view
				.fields(
					{
						size: true
					},
					(data) => {
						that.$set(that, 'clientHeight', data.height + 20)
					}
				)
				.exec()
		},

		/**
     * 获取产品详情
     *
     */

		getGoodsDetails() {
			const that = this
			getProductDetail(that.id)
				.then((res) => {
					const storeInfo = res.data.storeInfo
					const good_list = res.data.good_list || []
					const count = Math.ceil(good_list.length / 6)
					const goodArray = new Array()
					for (let i = 0; i < count; i++) {
						const list = good_list.slice(i * 6, i * 6 + 6)
						if (list.length) {
							goodArray.push({
								list
							})
						}
					}
					that.$set(that, 'storeInfo', storeInfo)
					that.$set(that, 'description', storeInfo.description)
					this.description = this.description.replace(
						/<img/gi,
						'<img style="max-width:100%;height:auto;float:left;display:block" '
					)
					that.$set(that, 'reply', res.data.reply ? [ res.data.reply ] : [])
					that.$set(that, 'replyCount', res.data.replyCount)
					that.$set(that, 'replyChance', res.data.replyChance)
					that.$set(that.attr, 'productAttr', res.data.productAttr)
					that.$set(that, 'productValue', res.data.productValue)
					that.$set(that, 'is_vip', res.data.storeInfo.is_vip)
					that.$set(that.sharePacket, 'priceName', res.data.priceName)
					that.$set(
						that.sharePacket,
						'isState',
						res.data.priceName != 0
					)
					that.$set(that, 'systemStore', res.data.system_store)
					that.$set(that, 'storeSelfMention', res.data.store_self_mention)
					that.$set(that, 'good_list', goodArray)
					that.$set(that, 'PromotionCode', storeInfo.code_base)
					that.$set(
						that,
						'activity',
						res.data.activity ? res.data.activity : []
					)
					that.$set(that, 'couponList', res.data.coupons)
					that.$set(
						that,
						'routineContact',
						Number(res.data.routine_contact_type)
					)
					uni.setNavigationBarTitle({
						title: storeInfo.store_name.substring(0, 7) + '...'
					})
					for (const key in res.data.productValue) {
						const obj = res.data.productValue[key]
						that.skuArr.push(obj)
					}
					this.$set(this, 'selectSku', that.skuArr[0])
					console.log(this.selectSku, this.skuArr)
					that.$set(
						that,
						'diff',
						that.$util.$h.Sub(storeInfo.price, storeInfo.vip_price)
					)
					var navList = ['商品', '详情']
					if (res.data.replyCount) {
						navList.splice(1, 0, '评价')
					}
					// if (goodArray.length) {
					// 	navList.splice(-1, 0, '推荐');
					// }
					that.$set(that, 'navList', navList)
					// #ifdef H5 || APP-PLUS
					that.$set(that, 'storeImage', that.storeInfo.image)
					// #endif
					// #ifdef H5
					that.getImageBase64()
					if (that.isLogin) {
						that.ShareInfo()
					}
					// #endif
					if (that.isLogin) {
						that.getUserInfo()
					}
					this.$nextTick(() => {
						if (good_list.length) {
							// #ifndef APP-PLUS
							that.setClientHeight()
							// #endif
							// #ifdef APP-PLUS
							setTimeout(() => {
								that.setClientHeight()
							}, 1000)

							// #endif
						}
					})
					setTimeout(function () {
						that.infoScroll()
					}, 500)
					// #ifndef H5
					that.downloadFilestoreImage()
					// #endif
					that.DefaultSelect()
					that.getCartCount()
					setTimeout(() => {
						this.showSkeleton = false
					}, 1000)
				})
				.catch((err) =>
				// 状态异常返回上级页面
					that.$util.Tips(
						{
							title: err.toString()
						},
						{
							tab: 3,
							url: 1
						}
					))
		},
		infoScroll() {
			var that = this
			var topArr = []
			var heightArr = []
			for (var i = 0; i < that.navList.length; i++) {
				// productList
				// 获取元素所在位置
				var query = uni.createSelectorQuery().in(this)
				var idView = '#past' + i
				if (
					(!this.replyCount && !that.good_list.length && i == 1) ||
          (this.replyCount && !that.good_list.length && i == 2) ||
          (!this.replyCount && that.good_list.length && i == 2)
				) {
					idView = '#past' + 3
				}
				if (!this.replyCount && that.good_list.length && i == 1) {
					idView = '#past' + 2
				}
				query.select(idView).boundingClientRect()
				query.exec(function (res) {
					var top = res[0].top
					var height = res[0].height
					topArr.push(top)
					heightArr.push(height)
					that.$set(that, 'topArr', topArr)
					that.$set(that, 'heightArr', heightArr)
				})
			}
		},

		/**
     * 拨打电话
     */

		makePhone() {
			uni.makePhoneCall({
				phoneNumber: this.systemStore.phone
			})
		},

		/**
     * 打开地图
     *
     */

		showMaoLocation() {
			if (!this.systemStore.latitude || !this.systemStore.longitude) {
				return this.$util.Tips({
					title: '缺少经纬度信息无法查看地图！'
				})
			}
			uni.openLocation({
				latitude: parseFloat(this.systemStore.latitude),
				longitude: parseFloat(this.systemStore.longitude),
				scale: 8,
				name: this.systemStore.name,
				address: this.systemStore.address + this.systemStore.detailed_address,
				success() {}
			})
		},

		/**
     * 默认选中属性
     *
     */

		DefaultSelect() {
			const productAttr = this.attr.productAttr
			let value = []
			for (var key in this.productValue) {
				if (this.productValue[key].stock > 0) {
					value = this.attr.productAttr.length ? key.split(',') : []
					break
				}
			}
			for (let i = 0; i < productAttr.length; i++) {
				this.$set(productAttr[i], 'index', value[i])
			}
			// sort();排序函数:数字-英文-汉字；
			const productSelect = this.productValue[value.join(',')]
			if (productSelect && productAttr.length) {
				this.$set(
					this.attr.productSelect,
					'store_name',
					this.storeInfo.store_name
				)
				this.$set(this.attr.productSelect, 'image', productSelect.image)
				this.$set(this.attr.productSelect, 'price', productSelect.price)
				this.$set(this.attr.productSelect, 'stock', productSelect.stock)
				this.$set(this.attr.productSelect, 'unique', productSelect.unique)
				this.$set(this.attr.productSelect, 'cart_num', 1)
				this.$set(this, 'attrValue', value.join(','))
				this.$set(
					this.attr.productSelect,
					'vip_price',
					productSelect.vip_price
				)
				this.$set(this, 'attrTxt', '已选择')
			} else if (!productSelect && productAttr.length) {
				this.$set(
					this.attr.productSelect,
					'store_name',
					this.storeInfo.store_name
				)
				this.$set(this.attr.productSelect, 'image', this.storeInfo.image)
				this.$set(this.attr.productSelect, 'price', this.storeInfo.price)
				this.$set(this.attr.productSelect, 'stock', 0)
				this.$set(this.attr.productSelect, 'unique', '')
				this.$set(this.attr.productSelect, 'cart_num', 0)
				this.$set(
					this.attr.productSelect,
					'vip_price',
					this.storeInfo.vip_price
				)
				this.$set(this, 'attrValue', '')
				this.$set(this, 'attrTxt', '请选择')
			} else if (!productSelect && !productAttr.length) {
				this.$set(
					this.attr.productSelect,
					'store_name',
					this.storeInfo.store_name
				)
				this.$set(this.attr.productSelect, 'image', this.storeInfo.image)
				this.$set(this.attr.productSelect, 'price', this.storeInfo.price)
				this.$set(this.attr.productSelect, 'stock', this.storeInfo.stock)
				this.$set(
					this.attr.productSelect,
					'unique',
					this.storeInfo.unique || ''
				)
				this.$set(this.attr.productSelect, 'cart_num', 1)
				this.$set(
					this.attr.productSelect,
					'vip_price',
					this.storeInfo.vip_price
				)
				this.$set(this, 'attrValue', '')
				this.$set(this, 'attrTxt', '请选择')
			}
		},

		/**
     * 获取优惠券
     *
     */

		getCouponList(type) {
			const that = this
			const obj = {
				page: 1,
				limit: 20,
				product_id: that.id
			}
			if (type !== undefined || type !== null) {
				obj.type = type
			}
			getCoupons(obj).then((res) => {
				that.$set(that.coupon, 'count', res.data.count)
				if (type === undefined || type === null) {
					const count = [ ...that.coupon.count ]
					let indexs = ''
					const index = count.findIndex((item) => item)
					const delCount = that.coupon.count
					const newDelCount = []
					let countIndex = 0
					delCount.forEach((item, index) => {
						if (item === 0) {
							countIndex = index
						} else {
							newDelCount.push(item)
						}
					})
					if (newDelCount.length == 3) {
						indexs = 2
					} else if (newDelCount.length == 2) {
						if (countIndex === 2) {
							indexs = 1
						} else {
							indexs = 2
						}
					} else {
						indexs = delCount.findIndex((item) => item === count[index])
					}
					that.$set(that.coupon, 'type', indexs)
					that.getCouponList(indexs)
				} else {
					that.$set(that.coupon, 'list', res.data.list)
				}
			})
		},
		ChangCouponsUseState(index) {
			const that = this
			that.coupon.list[index].is_use = true
			that.$set(that.coupon, 'list', that.coupon.list)
			that.$set(that.coupon, 'coupon', false)
		},

		/**
     *
     *
     * 收藏商品
     */

		setCollect() {
			if (this.isLogin === false) {
				toLogin()
			} else {
				const that = this
				if (this.storeInfo.userCollect) {
					collectDel(this.storeInfo.id).then((res) => {
						that.$set(
							that.storeInfo,
							'userCollect',
							!that.storeInfo.userCollect
						)
						return that.$util.Tips({
							title: res.msg
						})
					})
				} else {
					collectAdd(this.storeInfo.id).then((res) => {
						that.$set(
							that.storeInfo,
							'userCollect',
							!that.storeInfo.userCollect
						)
						return that.$util.Tips({
							title: res.msg
						})
					})
				}
			}
		},

		/**
     * 打开属性插件
     */

		selecAttr() {
			this.$set(this.attr, 'cartAttr', true)
			this.$set(this, 'isOpen', true)
		},

		/**
     * 打开优惠券插件
     */

		couponTap() {
			const that = this
			if (that.isLogin === false) {
				toLogin()
			} else {
				that.getCouponList()
				that.$set(that.coupon, 'coupon', true)
			}
		},
		onMyEvent() {
			this.$set(this.attr, 'cartAttr', false)
			this.$set(this, 'isOpen', false)
		},

		/**
     * 打开属性加入购物车
     *
     */

		joinCart(e) {
			// 是否登录
			if (this.isLogin === false) {
				toLogin()
			} else {
				this.goCat()
			}
		},

		/*
     * 加入购物车
     */

		goCat(news) {
			const that = this
			const productSelect = that.productValue[this.attrValue]
			// 打开属性
			if (that.attrValue) {
				// 默认选中了属性，但是没有打开过属性弹窗还是自动打开让用户查看默认选中的属性
				that.attr.cartAttr = !that.isOpen
			} else if (that.isOpen) that.attr.cartAttr = true
			else that.attr.cartAttr = !that.attr.cartAttr
			// 只有关闭属性弹窗时进行加入购物车
			if (that.attr.cartAttr === true && that.isOpen === false) return (that.isOpen = true)
			// 如果有属性,没有选择,提示用户选择
			if (
				that.attr.productAttr.length &&
        productSelect === undefined &&
        that.isOpen === true
			) {
				return that.$util.Tips({
					title: '产品库存不足，请选择其它属性'
				})
			}
			if (that.attr.productSelect.cart_num <= 0) {
				that.attr.productSelect.cart_num = 1
				that.isOpen = false
				return that.$util.Tips({
					title: '请先选择数量'
				})
			}
			const q = {
				productId: that.id,
				cartNum: that.attr.productSelect.cart_num,
				new: news === undefined ? 0 : 1,
				uniqueId:
          that.attr.productSelect !== undefined
          	? that.attr.productSelect.unique
          	: '',
				virtual_type: that.storeInfo.virtual_type
			}
			postCartAdd(q)
				.then(function (res) {
					that.isOpen = false
					that.attr.cartAttr = false
					if (news) {
						uni.navigateTo({
							url:
                '/pages/users/order_confirm/index?new=1&cartId=' +
                res.data.cartId
						})
					} else {
						that.$util.Tips({
							title: '添加购物车成功',
							success: () => {
								that.getCartCount(true)
							}
						})
					}
				})
				.catch((err) => {
					that.isOpen = false
					return that.$util.Tips({
						title: err
					})
				})
		},

		/**
     * 获取购物车数量
     * @param boolean 是否展示购物车动画和重置属性
     */

		getCartCount(isAnima) {
			const that = this
			const isLogin = that.isLogin
			if (isLogin) {
				getCartCounts().then((res) => {
					console.log()
					that.CartCount = res.data.count
					this.$store.commit(
						'indexData/setCartNum',
						that.CartCount > 99 ? '...' : that.CartCount + ''
					)
					// uni.setTabBarBadge({
					// 	index: Number(uni.getStorageSync('FOOTER_ADDCART')) || 2,
					// 	text: that.CartCount + ''
					// })
					// 加入购物车后重置属性
					if (isAnima) {
						that.animated = true
						setTimeout(function () {
							that.animated = false
						}, 500)
					}
				})
			}
		},

		/**
     * 立即购买
     */

		goBuy(e) {
			// console.log(111)
			if (this.isLogin === false) {
				toLogin()
			} else {
				this.goCat(true)
			}
		},
		open(data) {
			this.showMenuIcon = data
		},
		// 授权关闭
		authColse(e) {
			this.isShowAuth = e
		},

		/**
     * 分享打开
     *
     */

		listenerActionSheet() {
			if (this.isLogin === false) {
				toLogin()
			} else {
				// #ifdef H5
				if (this.$wechat.isWeixin() === true) {
					this.weixinStatus = true
				}
				// #endif
				// #ifndef APP-PLUS
				this.downloadFilePromotionCode()
				// #endif
				// #ifdef APP-PLUS
				if (this.PromotionCode.indexOf('http') == 0) {
					this.downloadFilePromotionCode()
				}
				// #endif

				this.posters = true
			}
		},
		// 分享关闭
		listenerActionClose() {
			this.posters = false
		},
		// 隐藏海报
		posterImageClose() {
			this.posterImageStatus = false
		},
		// 替换安全域名
		setDomain(url) {
			url = url ? url.toString() : ''
			// 本地调试打开,生产请注销
			if (url.indexOf('https://') > -1) return url
			return url.replace('http://', 'https://')
		},
		// 获取海报产品图
		downloadFilestoreImage() {
			const that = this
			uni.downloadFile({
				url: that.setDomain(that.storeInfo.image),
				success(res) {
					that.storeImage = res.tempFilePath
				},
				fail() {
					return that.$util.Tips({
						title: ''
					})
					that.storeImage = ''
				}
			})
		},

		/**
     * 获取产品分销二维码
     * @param function successFn 下载完成回调
     *
     */

		downloadFilePromotionCode(successFn) {
			const that = this
			// #ifdef H5 || MP
			getProductCode(that.id)
				.then((res) => {
					uni.downloadFile({
						url: that.setDomain(res.data.code),
						success(res) {
							that.$set(that, 'isDown', false)
							if (typeof successFn === 'function') successFn && successFn(res.tempFilePath)
							else that.$set(that, 'PromotionCode', res.tempFilePath)
						},
						fail() {
							that.$set(that, 'isDown', false)
							that.$set(that, 'PromotionCode', '')
						}
					})
				})
				.catch((err) => {
					that.$set(that, 'isDown', false)
					that.$set(that, 'PromotionCode', '')
				})
			// #endif
			// #ifdef APP-PLUS
			uni.downloadFile({
				url: that.setDomain(that.PromotionCode),
				success(res) {
					that.$set(that, 'isDown', false)
					if (typeof successFn === 'function') successFn && successFn(res.tempFilePath)
					else that.$set(that, 'PromotionCode', res.tempFilePath)
				},
				fail() {
					that.$set(that, 'isDown', false)
					that.$set(that, 'PromotionCode', '')
				}
			})
			// #endif
		},
		getImageBase64() {
			const that = this
			imageBase64(that.storeImage, that.PromotionCode)
				.then((res) => {
					that.storeImage = res.data.image
					that.PromotionCode = res.data.code
				})
				.catch(() => {})
		},
		// 小程序关闭分享弹窗；
		goFriend() {
			this.posters = false
		},

		/**
     * 生成海报
     */

		goPoster() {
			const that = this
			that.posters = false

			that.$set(that, 'canvasStatus', true)
			const arr2 = [that.posterbackgd, that.storeImage, that.PromotionCode]

			// // #ifndef H5
			if (that.isDown) {
				return that.$util.Tips({
					title: '正在下载海报,请稍后再试！'
				})
			}
			// // #endif
			uni.getImageInfo({
				src: that.PromotionCode,
				fail(res) {
					// #ifdef H5
					return that.$util.Tips({
						title: res
					})
					// #endif
					// #ifdef MP
					return that.$util.Tips({
						title: '小程序二维码需要发布正式版后才能获取到'
					})
					// #endif
				},
				success() {
					if (arr2[2] == '') {
						// 海报二维码不存在则从新下载
						// // #ifndef H5
						that.downloadFilePromotionCode(function (msgPromotionCode) {
							arr2[2] = msgPromotionCode
							if (arr2[2] == '') {
								return that.$util.Tips({
									title: '海报二维码生成失败！'
								})
							}
							that.$util.PosterCanvas(
								arr2,
								that.storeInfo.store_name,
								that.storeInfo.price,
								that.storeInfo.ot_price,
								function (tempFilePath) {
									that.$set(that, 'posterImage', tempFilePath)
									that.$set(that, 'posterImageStatus', true)
									that.$set(that, 'canvasStatus', false)
									that.$set(that, 'actionSheetHidden', !that.actionSheetHidden)
								}
							)
						})
						// // #endif
						// // #ifdef H5
						// that.$util.PosterCanvas(arr2, that.storeInfo.store_name, that.storeInfo.price, function(tempFilePath) {
						// 	that.$set(that, 'posterImage', tempFilePath);
						// 	that.$set(that, 'posterImageStatus', true);
						// 	that.$set(that, 'canvasStatus', false);
						// 	that.$set(that, 'actionSheetHidden', !that.actionSheetHidden);
						// });
						// // #endif
					} else {
						// 生成推广海报
						that.$util.PosterCanvas(
							arr2,
							that.storeInfo.store_name,
							that.storeInfo.price,
							that.storeInfo.ot_price,
							function (tempFilePath) {
								that.$set(that, 'posterImage', tempFilePath)
								that.$set(that, 'posterImageStatus', true)
								that.$set(that, 'canvasStatus', false)
								that.$set(that, 'actionSheetHidden', !that.actionSheetHidden)
							}
						)
					}
				}
			})
		},

		/*
     * 保存到手机相册
     */
		// #ifdef MP
		copyCommand() {
			if (wx.navigateToMiniProgram) {
				wx.navigateToMiniProgram({
					appId: 'wxb036cafe2994d7d0',
					path: '/publish/ugc-publish/ugc-publish',
					extraData: {
						productInfo: {
							title: this.storeInfo.store_name,
							path: '/pages/goods_details/index?id=' + this.storeInfo.id,
							thumbUrl: this.storeInfo.image
						}
					}
				})
			}
		},
		savePosterPath() {
			const that = this
			uni.getSetting({
				success(res) {
					if (!res.authSetting['scope.writePhotosAlbum']) {
						uni.authorize({
							scope: 'scope.writePhotosAlbum',
							success() {
								uni.saveImageToPhotosAlbum({
									filePath: that.posterImage,
									success(res) {
										that.posterImageClose()
										that.$util.Tips({
											title: '保存成功',
											icon: 'success'
										})
									},
									fail(res) {
										that.$util.Tips({
											title: '保存失败'
										})
									}
								})
							}
						})
					} else {
						uni.saveImageToPhotosAlbum({
							filePath: that.posterImage,
							success(res) {
								that.posterImageClose()
								that.$util.Tips({
									title: '保存成功',
									icon: 'success'
								})
							},
							fail(res) {
								that.$util.Tips({
									title: '保存失败'
								})
							}
						})
					}
				}
			})
		},
		// #endif
		// #ifdef APP-PLUS
		savePosterPath() {
			const that = this
			uni.saveImageToPhotosAlbum({
				filePath: that.posterImage,
				success(res) {
					that.posterImageClose()
					that.$util.Tips({
						title: '保存成功',
						icon: 'success'
					})
				},
				fail(res) {
					that.$util.Tips({
						title: '保存失败'
					})
				}
			})
		},
		// #endif

		// #ifdef H5
		ShareInfo() {
			const data = this.storeInfo
			let href = location.href
			if (this.$wechat.isWeixin()) {
				getUserInfo().then((res) => {
					href =
            href.indexOf('?') === -1
            	? href + '?spread=' + res.data.uid
            	: href + '&spread=' + res.data.uid

					const configAppMessage = {
						desc: data.store_info,
						title: data.store_name,
						link: href,
						imgUrl: data.image
					}
					this.$wechat
						.wechatEvevt(
							[
								'updateAppMessageShareData',
								'updateTimelineShareData',
								'onMenuShareAppMessage',
								'onMenuShareTimeline'
							],
							configAppMessage
						)
						.then((res) => {})
						.catch((err) => {})
				})
			}
		},
		// #endif
		tabCouponType(type) {
			this.$set(this.coupon, 'type', type)
			this.getCouponList(type)
		},
		// 点击sku图片打开轮播图
		showImg(index) {
			this.$refs.cusPreviewImg.open(this.selectSku.suk)
		},
		// 滑动轮播图选择商品
		changeSwitch(e) {
			console.log(this.skuArr[e])
			const productSelect = this.skuArr[e]
			this.$set(this, 'selectSku', productSelect)
			var skuList = productSelect.suk.split(',')
			console.log(this.attr.productAttr)
			this.$set(this.attr.productAttr[0], 'index', skuList[0])

			if (skuList.length == 2) {
				this.$set(this.attr.productAttr[0], 'index', skuList[0])
				this.$set(this.attr.productAttr[1], 'index', skuList[1])
			} else if (skuList.length == 3) {
				this.$set(this.attr.productAttr[0], 'index', skuList[0])
				this.$set(this.attr.productAttr[1], 'index', skuList[1])
				this.$set(this.attr.productAttr[2], 'index', skuList[2])
			} else if (skuList.length == 4) {
				this.$set(this.attr.productAttr[0], 'index', skuList[0])
				this.$set(this.attr.productAttr[1], 'index', skuList[1])
				this.$set(this.attr.productAttr[2], 'index', skuList[2])
				this.$set(this.attr.productAttr[3], 'index', skuList[3])
			}
			if (productSelect) {
				this.$set(this.attr.productSelect, 'image', productSelect.image)
				this.$set(this.attr.productSelect, 'price', productSelect.price)
				this.$set(this.attr.productSelect, 'stock', productSelect.stock)
				this.$set(this.attr.productSelect, 'unique', productSelect.unique)
				this.$set(this.attr.productSelect, 'vipPrice', productSelect.vipPrice)
				this.$set(this, 'attrTxt', '已选择')
				this.$set(this, 'attrValue', productSelect.suk)
			}
		}
	}
}
</script>

